import Events from '../../event/Events';

/**
 * requestAnimationFrame  singleton  {@link Cycle} Events
 */
export default class CycleEvents extends Events {
  // ---------------------------------------------------
  //  CONSTRUCTOR
  // ---------------------------------------------------
  /**
   * custom Event Object
   * @param {string} type 
   * @param {*} currentTarget current 
   * @param {*} [target=undefined] 
   * */
  constructor(type, currentTarget, target = undefined) {
    super(type, currentTarget, target);
    // ---
    /**
     * requestAnimationFrame 
     * @type {number}
     */
    this.id = -1;
    /**
     * animation time
     * @type {number}
     * @since 2018-01-20
     */
    this.time = 0;
  }
}
