import Events from '../../event/Events';

/**
 * {@link Polling} Events
 */
export default class PollingEvents extends Events {
  // ---------------------------------------------------
  //  CONSTRUCTOR
  // ---------------------------------------------------
  /**
   * custom Event Object
   * @param {string} type 
   * @param {*} currentTarget current 
   * @param {*} [target=undefined] 
   * */
  constructor(type, currentTarget, target = undefined) {
    super(type, currentTarget, target);
    // ---
    /**
     * polling event 
     * @type {number}
     */
    this.begin = 0;
    /**
     *  - Date.now
     * @type {number}
     */
    this.present = 0;
    /**
     * (ms)
     * @type {number}
     */
    this.interval = 0;
    /**
     * CycleEvents
     * @type {CycleEvents}
     * @since 2018-01-20
     */
    this.cycleEvents = 0;
  }
}
