/**
 * {@link Times}
 *  - 1 minute
 * ```
 * 1000 * 60
 * ```
 * @type {number}
 * @private
 * @static
 */
const oneMinute = 1000 * 60;
/**
 * {@link Times}
 *  - 1 hour
 * ```
 * 1000 * 60 * 60
 * ```
 * @type {number}
 * @private
 * @static
 */
const oneHour = oneMinute * 60;
/**
 * {@link Times}
 *  - 1 day
 * ```
 * 1000 * 60 * 60 * 24
 * ```
 * @type {number}
 * @private
 * @static
 */
const oneDay = oneHour * 24;
/**
 * {@link Times}
 *  - 1 week
 * ```
 * 1000 * 60 * 60 * 24 * 7
 * ```
 * @type {number}
 * @private
 * @static
 */
const oneWeek = oneDay * 7;
/**
 * {@link Times}
 *  - 1 month
 * ```
 * 1000 * 60 * 60 * 24 * 30
 * ```
 * @type {number}
 * @private
 * @static
 */
const oneMonth = oneDay * 30;

/**
 *  Date instance 
 */
export default class Times {
  // /**
  //  * 1970-1-1 00:00
  //  * @returns {Date} 1970-1-1 00:00
  //  */
  // static standard() {
  //   return new Date();
  // }
  /**
   *  Date instance
   * @returns {Date}  Date instance
   */
  static present() {
    return new Date();
  }

  /**
   *  Date instance
   * @param {number} n 
   * @returns {Date}  Date instance
   */
  static minute(n) {
    return new Date(Date.now() + n * oneMinute);
  }

  /**
   *  Date instance
   * @param {number} n 
   * @returns {Date}  Date instance
   */
  static hour(n) {
    return new Date(Date.now() + n * oneHour);
  }

  /**
   *  Date instance
   * @param {number} n 
   * @returns {Date}  Date instance
   */
  static day(n) {
    return new Date(Date.now() + n * oneDay);
  }

  /**
   *  Date instance
   * @param {number} n 
   * @returns {Date}  Date instance
   */
  static week(n) {
    return new Date(Date.now() + n * oneWeek);
  }

  /**
   *  Date instance
   * @param {number} n 
   * @returns {Date}  Date instance
   */
  static month(n) {
    return new Date(Date.now() + n * oneMonth);
  }
}
