import Resizing from './Resizing';

/**
 * singleton Resizing instance
 * @type {?Resizing}
 */
let instance = null;

/**
 * Resizing instance  singleton 
 */
export default class ResizingSingle {
  // ----------------------------------------
  // STATIC METHOD
  // ----------------------------------------
  /**
   * Resizing instance  singleton 
   * @returns {Resizing} Resizing instance
   */
  static factory() {
    if (!instance) {
      instance = new Resizing();
    }
    return instance;
  }
}
