import Events from '../Events';

/**
 * {@link Rising} Events
 */
export default class RisingEvents extends Events {
  // ---------------------------------------------------
  //  CONSTRUCTOR
  // ---------------------------------------------------
  /**
   * custom Event Object
   * @param {string} type 
   * @param {*} currentTarget current 
   * @param {*} [target=undefined] 
   * */
  constructor(type, currentTarget, target = undefined) {
    super(type, currentTarget, target);
    // ---
    /**
     * , true: 
     * @type {boolean}
     */
    this.hit = false;
    /**
     * original event
     * @type {?Event|*}
     */
    this.original = null;
    /**
     * ClientRect
     * @type {?ClientRect}
     */
    this.offset = null;
  }
}
